.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 16vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.rect {

    cursor: pointer;
  }


.stone-glow{
    border-radius: 50%;
    box-shadow: 0px 0px 9px 4px #747DE8;
    -webkit-animation: glow 1.5s linear infinite alternate;
            animation: glow 1.5s linear infinite alternate;
}

  @-webkit-keyframes glow{
    to {
      box-shadow: 0px 0px 30px 20px #535FED;
    }
  }

  @keyframes glow{
    to {
      box-shadow: 0px 0px 30px 20px #535FED;
    }
  }

.wrapper {
    display: grid;
    grid-template-columns: 56px 55px 55px;
    min-height: 150px;
    border-radius: 50%;
    padding: -18px;
    background-color: slategrey;
  }
  
  .box {

    border-radius: 50%;
   
  }
